.pins {
    position: absolute;
    width: 0px;
    height: 0px;
    left: 50%;
    top: 50%;
    z-index: 1;
    .pin{
        display: flex;
        align-items: center;
        gap: 12px;
        position: absolute;
        width: max-content;
        cursor: pointer;
        .pin-icon {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 36px;
            height: 36px;
            border: 1px solid $cl-black;
            border-radius: 50%;
            background: $cl-white;
            img {
                width: 24px;
                height: 24px;
            }
        }
        .pin-title {
            transition: 0.3s ease-in-out;
            opacity: 1;
        }
    }
    .f18-20-pin-1 {top: 10vh;  left: -1vw;}
    .f18-20-pin-2 {top: 32vh;  left: -1vw;}
    .f16-17-pin-1 {top: 18vh;  left: 2vw;}
    .f14-15-pin-1 {}
    .f5-13-pin-1 {}
    .f3-4-pin-1 {}
    .f2-pin-1 {}
    .b1-f1-pin-1 {top: 100px;  left: 100px;}
    .b1-f1-pin-2 {top: -100px;  left: -100px;}
    .b2-facility-pin-1 {}
    .b3-facility-pin-1 {}
    .b4-facility-pin-1 {}
    
    .visible {
        opacity: 1;
        transition: 0.5s ease;
        transition-delay: 0.5s;
    }
    
    .unvisible {
        opacity: 0;
        transition: 0.1s ease;
    }
}