.button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background: $cl-gray02;
    transition: 0.3s ease;
    cursor: pointer;
    img {
        width: 32px;
        height: 32px;
        cursor: pointer;
    }
    &:hover {
        background: $cl-gray01;
    }
}