@import './HomeNavigation/home-navigation.scss';
@import './HomePopup/home-popup.scss';

.section-information {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;

    .floor-container {
        position: absolute;
        top: 0;
        left: calc(50% - 9vw);
        height: 100vh;
        padding: 60px 0;

        .floors {
            position: absolute;
            display: flex;
            align-items: center;
            gap: 14vw;

            .floor-number {
                width: 60px;
                p {
                    color: $cl-gray05;
                }
            }

            .floor-pin {
                display: flex;
                align-items: center;
                width: max-content;
                transition: 0.3s ease;
                opacity: 0.6;
                .pin-head {
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    background: $cl-black;
                }
                .pin-line {
                    width: 140px;
                    height: 1px;
                    background: $cl-black;
                    border: none;
                    transition: 0.3s ease;
                }
                .pin-icon {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 32px;
                    height: 32px;
                    border-radius: 50%;
                    border: 1px solid $cl-black;
                    img {
                        width: 24px;
                        height: 24px;
                    }
                }
                .pin-title {
                    padding: 8px;
                }

                &:hover {
                    opacity: 1;
                    .pin-line {
                        width: 148px;
                    }
                }
            }
            // 18-20
            &:nth-child(1) { top: 13vh; }
            // 16-17
            &:nth-child(2) { top: 21vh; }
            // 14-15
            &:nth-child(3) { top: 29vh; }
            // 5-13
            &:nth-child(4) { top: 50vh; }
            // 3-4
            &:nth-child(5) { top: 50vh; }
            // 2
            &:nth-child(5) { top: 69vh; }
            // b1-1
            &:nth-child(6) { top: 75vh; }
            // b3-b2
            &:nth-child(7) { top: 82vh; }
            &:nth-child(8) { top: 90vh; }
        }
    }
}

.fade-in {
    opacity: 1;
    transition: 0.3s;
    z-index: 100;
}
.fade-out {
    opacity: 0;
    transition: 0.3s;
    z-index: -100;
}

