.home-popup {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    height: 100vh;
    .popup-close-button {
        position: absolute;
        top: 76px;
        left: 52px;
    }
    .image-gallery {
        display: flex;
        width: 100%;
        .image-list {
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 4px;
            padding: 76px 52px;
            background: $cl-black;
            .list-item {
                width: max-content;
                color: $cl-gray03;
                transition: 0.3s ease;
                cursor: pointer;
                &:hover {
                    color: $cl-white;
                    padding-left: 4px;
                }
            }
            .list-item-selected {
                padding-left: 4px;
                color: $cl-white;
            }
        }
    }
    .image-area {
        width: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
        background: $cl-black;
    }
}

.show-home-popup {
    display: flex;
}