@import './floor-pins.scss';

.floor-map {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    .floor-content {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        opacity: 0;
        transition: 0.3s ease;
        img {
            height: 100vh;
        }
        .pins{
            .pin {
            }
        }
    }
    .show-floor-content {
        opacity: 1;
    }
}